html {
  position: relative;
  min-height: 100%;
}
body {
  margin: 0 0 60px;
  padding: 0;
  font-family: sans-serif;
  background:transparent
}

footer {
  position: fixed;
  left:0;
  bottom: 0;
  width: 100%;
  height: 60px; /* Set the fixed height of the footer here */
  line-height:60px;
  background-color: #f7f7f7;
  -webkit-box-shadow: 0 -2px 4px -4px #000;
          box-shadow: 0 -2px 4px -4px #000;
  font-size: 0.6rem;
  text-align:center;
}

.github-corner:hover .octo-arm{-webkit-animation:octocat-wave 560ms ease-in-out;animation:octocat-wave 560ms ease-in-out}
@-webkit-keyframes octocat-wave{
    0%,100%{-webkit-transform:rotate(0);transform:rotate(0)}
    20%,60%{-webkit-transform:rotate(-25deg);transform:rotate(-25deg)}
    40%,80%{-webkit-transform:rotate(10deg);transform:rotate(10deg)}
}
@keyframes octocat-wave{
    0%,100%{-webkit-transform:rotate(0);transform:rotate(0)}
    20%,60%{-webkit-transform:rotate(-25deg);transform:rotate(-25deg)}
    40%,80%{-webkit-transform:rotate(10deg);transform:rotate(10deg)}
}
@media (max-width:500px){
    .github-corner:hover .octo-arm{-webkit-animation:none;animation:none}
    .github-corner .octo-arm{-webkit-animation:octocat-wave 560ms ease-in-out;animation:octocat-wave 560ms ease-in-out}
}



